:root {
  --bg-dark: rgb(13, 13, 47);
  --bg-light: rgb(243, 243, 251);

  margin: 0;
  padding: 0;
  border: 0;
}

body{
  overflow-x: scroll;
}
.sidebar {
  flex: 1 1;
  border-right: 0.5px solid rgb(230, 227, 227);
  min-height: 100vh;
  background-color: white;
}
.top {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo {
  font-size: 20px;
  font-weight: bold;
  color: #6439ff;
}
hr {
  height: 0;
  border: 0.5px solid rgb(230, 227, 227);
}

.center {
  padding-left: 10px;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.title {
  font-size: 10px;
  font-weight: bold;
  color: #999;
  margin-top: 15px;
  margin-bottom: 5px;
}
li {
  display: flex;
  align-items: center;
  padding: 5px;
  cursor: pointer;
}
li:hover {
  background-color: #ece8ff;
}

.icon {
  font-size: 18px;
  color: #7451f8;
}
span {
  font-size: 13px;
  font-weight: 600;
  color: #888;
  margin-left: 10px;
}
.bottom {
  display: flex;
  align-items: center;
  margin: 10px;
}
.colorOption {
  width: 20px;
  height: 20px;
  border-radius: 5px;
  border: 1px solid #7451f8;
  cursor: pointer;
  margin: 5px;
}
.colorOption:nth-child(1) {
  background-color: whitesmoke;
}
.colorOption:nth-child(2) {
  background-color: #333;
}
.colorOption:nth-child(3) {
  background-color: darkblue;
}

.navbar {
  height: 55px;
  border-bottom: 0.5px solid rgb(231, 228, 228);
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #555;
}
.wrapper {
  width: 100%;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.search {
  display: flex;
  align-items: center;
  border: 0.5px solid lightgray;
  padding: 3px;
}
.search input {
  border: none;
  outline: none;
  background: transparent;
}
.search input::-webkit-input-placeholder {
  font-size: 12px;
}
.search input:-ms-input-placeholder {
  font-size: 12px;
}
.search input::placeholder {
  font-size: 12px;
}
.items {
  display: flex;
  align-items: center;
}

.navtoggle{
  cursor: pointer;
}
.item {
  display: flex;
  align-items: center;
  margin-right: 20px;
  position: relative;
}
.itemlast {
  display: flex;
  align-items: center;
  margin-right: 0;
  position: relative;
}

.icon {
  font-size: 20px;
}

.avatar {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
 .navbar .wrapper .counter {
  width: 15px;
  height: 15px;
  background-color: red;
  border-radius: 50%;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  font-weight: bold;
  position: absolute;
  top: -5px;
  right: -5px;
}

.home {
  display: grid;
  grid-template-columns: 20% 80%;
}
.homeContainer {
  display: flex;
  flex-direction: column;
}

.login-wrap {
  box-sizing: border-box;
  width: 90vw;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: auto auto;
  overflow: hidden;
  margin: 5%;
}

.loginContainer {
  background-color: var(--bg-dark);
  padding: 20px;
  box-shadow: 3px 4px 30px 1px rgba(46, 23, 15, 0.766);
}

.login {
  width: 500px;
  height: 500px;
  min-width: 100%;
  min-height: 500px;
  color: var(--bg-light);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.login form {
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.formInput {
  display: grid;
  padding: 20px;
  grid-template-columns: 1fr 2.5fr;
  align-items: center;
}
.formInput label {
  font-weight: 1000;
  font-size: 1.2rem;
}
.formInput label::after {
  content: " :";
  font-weight: 1000;
  font-size: 1.5rem;
}
.formInput input {
  width: 100%;
  font-size: 1.1em;
  font-weight: bolder;
  background-color: gainsboro;
}
.formBtn {
  display: grid;
  align-items: center;
  justify-items: center;
  padding: 10px 0px;
  grid-template-columns: 1fr 1fr 1fr;
}
.formBtn input {
  width: 60%;
}
.formBtn button[type="submit"] {
  grid-column: 2;
}
.formBtn button[type="reset"] {
  grid-column: 3;
}

.loginContainer hr {
  color: aliceblue;
  width: 90%;
  margin-bottom: -15px;
}

.loginContainer .login > p {
  text-align: center;
}

.loginContainer .login > p span {
  font-size: 1em;
  font-weight: 1000;
  font-style: oblique;
  text-transform: uppercase;
  color: white;
}

/* =========================
GRADIENT BORDERED BUTTONS
========================= */

.gradBtn-1 {
  font-weight: 1000;
  box-sizing: border-box;
  position: relative;
  z-index: 1;
  display: inline-block;
  padding: 5px 40px;
  font-size: 1.3rem;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  background-color: #e7eef1;
  border: 10px solid transparent;
  border-image: linear-gradient(to top right, orangered, yellow);
  border-image-slice: 1;
  color: orangered;
  outline: none;
  transition: all 0.4s ease;
}
.gradBtn-1:hover {
  background-size: 100%;
  background-image: linear-gradient(to top right, orangered, yellow);
  color: white;
}

.gradBtn-2 {
  font-weight: 1000;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  position: relative;
  z-index: 1;
  display: inline-block;
  padding: 10px 40px;
  font-size: 1.3rem;
  background-color: #e7eef1;
  border: 4px solid transparent;
  border-image: linear-gradient(to right, orangered 80%, transparent);
  border-image-slice: 1;
  color: orangered;
  outline: none;
  transition: all 0.4s ease;
}
.gradBtn-2:hover {
  background-size: 100%;
  background-image: linear-gradient(to right, orangered 50%, transparent);
  border-right-style: none;
  color: white;
}

/* =========================

      MEDIA QUERIES

========================= */

@media screen and (max-width: 700px) {
  .login-wrap {
    margin: 3%;
  }

  .loginContainer {
    padding: 10px;
  }

  .login {
    width: 400px;
    height: 500px;
    min-width: 100%;
    min-height: 500px;
  }

  .formInput {
    display: flex;
    flex-direction: column;
    padding: 10px;
    align-items: flex-start;
  }
  .formInput label {
    font-size: 1.1rem;
    margin-bottom: 5px;
  }

  .formInput input {
    font-size: 1em;
  }
  .formBtn {
    align-self: center;
    display: flex;
    flex-direction: row-reverse;
    width: 90%;
    align-items: center;
    justify-content: center;
  }
  .formBtn button {
    margin: 5px 3%;
    width: 100%;
  }

  .loginContainer .login > p span {
    display: block;
  }
}

@media screen and (max-width: 430px) {
  .login-wrap {
    margin: 1%;
  }
  .loginContainer {
    padding: 5px;
  }
  .login {
    width: 250px;
    height: 500px;
  }
  .formBtn {
    display: flex;
    flex-direction: column-reverse;
    width: 90%;
  }
}

.widget {
  display: flex;
  justify-content: space-between;
  flex: 1 1;
  padding: 10px;
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  border-radius: 10px;
  height: 100px;
}

.left,
.right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.left .title {
  font-weight: bold;
  font-size: 14px;
  color: rgb(160, 160, 160);
}

.widget .left .counter {
  font-size: 28px;
  font-weight: 300;
  display: grid;
}
.widget .left .link {
  width: -webkit-max-content;
  width: max-content;
  font-size: 12px;
  border-bottom: 1px solid gray;
}

.right .percentage {
  display: flex;
  align-items: center;
  font-size: 14px;
}
.right .percentage .positive {
  background-color: rgb(255, 153, 0);
  color: rgb(0, 200, 255);
  font-style: italic;
}

.right .percentage .negative {
  color: red;
}
.left .icon,
.right .icon {
  font-size: 18px;
  padding: 5px;
  border-radius: 5px;
  align-self: flex-end;
}

.chart {
  display: flex;
  width: 100%;
}
.chart .title {
  text-align: center;
  padding: 5px;
}

.chart .chart-bar,
.chart .doughnut {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
}
.chart .doughnut {
  width: 60%;
}

.table {
}
.table .cellWrapper {
  display: flex;
  align-items: center;
}
.table .cellWrapper .image {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin-right: 10px;
  object-fit: cover;
}
.table .status {
  padding: 5px;
  border-radius: 5px;
}
.table .status .Approved {
  color: green;
  background-color: rgba(0, 128, 0, 0.151);
}

.table .status .Pending {
  color: goldenrod;
  background-color: rgba(189, 189, 3, 0.103);
}

.home {
  display: flex;
}
.homeContainer {
  flex: 6 1;
}
.homeContainer .widgets,
.homeContainer .charts {
  display: flex;
  padding: 20px;
  grid-gap: 20px;
  gap: 20px;
}
.homeContainer .charts {
  padding: 5px 20px;
}

.listContainer {
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  padding: 20px;
  margin: 20px;
}
.listContainer .listTitle {
  font-weight: 500;
  color: gray;
  margin-bottom: 15px;
}
.chart {
  flex: 4 1;
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  padding: 10px;
  color: gray;
}
.list {
  display: flex;
  width: 100%;
}
.list .listContainer {
  flex: 6 1;
}

/* .single {
  width: 100%;
  padding: 10px;
} */

.singleContainer {
  display: grid;
  grid-template-rows: 1fr 1fr;
  align-items: stretch;
  justify-content: space-between;
  margin-top: 50px;
  padding-top: 80px;
  width: 100%;
  height: 100%;
}

.singleContainer .top {
  padding: 15px;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}

.singleContainer .top .left {
  flex: 1 1;
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  padding: 20px;
  position: relative;
}

.singleContainer .left .item {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}

.singleContainer .item .itemImg {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.singleContainer  .details .itemTitle {
  margin-bottom: 10px;
  color: #555;
}
.singleContainer .top .right {
  flex: 2 1;
}
.singleContainer .bottom {
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  padding: 20px;
  margin: 10px 20px;
}

.singleContainer .title {
  font-size: 16px;
  color: lightgray;
  margin-bottom: 20px;
}

.singleContainer  .detailItem {
  margin-bottom: 10px;
  font-size: 14px;
}
.singleContainer .detailItem .itemKey {
  font-weight: bold;
  color: gray;
  margin-right: 5px;
}

.singleContainer .detailItem .itemValue {
  font-weight: 300;
}

.singleContainer .top .left .editButton {
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px;
  font-size: 12px;
  color: #7451f8;
  background-color: #7551f818;
  cursor: pointer;
  border-radius: 0px 0px 0px 5px;
}

.app.dark {
  background-color: #111;
  color: rgb(156, 156, 156);
}

.app.dark .chartGrid {
  stroke: rgba(228, 228, 228, 0.219);
}

.app.dark .table {
  background-color: #111;
}

.app.dark .table .tableCell {
  color: gray;
}
.app.dark .navbar {
  color: #999;
  border-color: #333;
}

.app.dark .navbar .search {
  border-color: gray;
}

.app.dark .sidebar {
  background-color: #111;
  border-color: #333;
}
.app.dark .sidebar .top .logo {
  color: #999;
}

.app.dark hr {
  border-color: #333;
}
.app.dark ul li:hover {
  background-color: #333;
}

.app.dark ul li .icon {
  color: #999;
}

.datatable .datagrid {
  color: gray;
  border: none;
}
.datatable .viewButton,
.datatable .deleteButton,
.datatable .cellWithStatus {
  color: gray;
  border: none;
}
.app.dark input {
  background-color: transparent;
}

