.widget {
  display: flex;
  justify-content: space-between;
  flex: 1;
  padding: 10px;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  border-radius: 10px;
  height: 100px;
}

.left,
.right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.left .title {
  font-weight: bold;
  font-size: 14px;
  color: rgb(160, 160, 160);
}

.widget .left .counter {
  font-size: 28px;
  font-weight: 300;
  display: grid;
}
.widget .left .link {
  width: max-content;
  font-size: 12px;
  border-bottom: 1px solid gray;
}

.right .percentage {
  display: flex;
  align-items: center;
  font-size: 14px;
}
.right .percentage .positive {
  background-color: rgb(255, 153, 0);
  color: rgb(0, 200, 255);
  font-style: italic;
}

.right .percentage .negative {
  color: red;
}
.left .icon,
.right .icon {
  font-size: 18px;
  padding: 5px;
  border-radius: 5px;
  align-self: flex-end;
}
