.table {
}
.table .cellWrapper {
  display: flex;
  align-items: center;
}
.table .cellWrapper .image {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin-right: 10px;
  object-fit: cover;
}
.table .status {
  padding: 5px;
  border-radius: 5px;
}
.table .status .Approved {
  color: green;
  background-color: rgba(0, 128, 0, 0.151);
}

.table .status .Pending {
  color: goldenrod;
  background-color: rgba(189, 189, 3, 0.103);
}
